import AppViewModel = require("./appViewModel");
import View = require("./view");
import KnockoutInteraction = require("./bindingHandlers/knockout-interaction");
import AppCache = require("./appCache");

class Main {
    appViewModel:AppViewModel
    initTime:Date

    bindViewModel():void {
        this.appViewModel = new AppViewModel(document.body);
        let viewContainer = document.getElementById("views");

        // viewContainer is hidden by default
        // set it to visible now that everything is loaded
        viewContainer.style.display = "block";
    }

    onPageReady(error:boolean) {
        this.appViewModel.logo.showStatus("Loading...");
        // show logo for at least 2 seconds
        // deduct loading time from 2 seconds to get delay we should apply,
        // make sure it's not a negative number (if loading took longer)
        let loadTime = new Date().getTime() - this.initTime.getTime();
        let delay = 2000 - loadTime;
        delay = delay > 0 ? delay : 0;
        setTimeout(() => {
            // don't try and login if offline
            if (!window.hasOwnProperty("gapi") || error || !navigator.onLine || window.location.href.indexOf("file://") === 0)
                this.appViewModel.currentView(View.selectPlayers);
            else
                this.appViewModel.currentView(View.login);
        }, delay);
    }

    constructor() {
        if(window.location.href.match(/^http:/))
            window.location.href = window.location.href.replace(/^http:/, "https:"); // nobody got time for that

        this.initTime = new Date();
        new KnockoutInteraction(); // add touch bindings
        this.bindViewModel(); // show logo
        this.appViewModel.logo.showStatus("Loading...");
        AppCache.processUpdates(this.appViewModel, (error) => { this.onPageReady(error); });
    }
}

new Main();