import * as ko from "knockout";
import Comparer = require("./../utilities/comparer")

class PlayerScore {
    // index in Data.players
    id: KnockoutObservable<number>
    // 0-100
    score: KnockoutObservable<number>
    // tally of how many times the player has lost
    gamesLost: KnockoutObservable<number>
    // bound to hasFocus ko binding
    hasFocus: KnockoutObservable<boolean>

    static sortByScore:(a: PlayerScore, b: PlayerScore) => number = Comparer.getComparer((p:PlayerScore) => p.score());
    
    // TODO: move hasScore/onFocus to enterRoundScores.ts?
    static hasScore(playerScore: PlayerScore) {
        return playerScore.score() !== undefined && playerScore.score().toString() !== "";
    }

    onFocus() {
        if(this.hasFocus() && this.score() === 0) {
            this.score(undefined);
        }
        if(!this.hasFocus() && (this.score() === undefined || this.score().toString() === "")) {
            this.score(0);
        }
    }

    constructor(id: number) {
        this.id = ko.observable<number>(id);
        this.score = ko.observable<number>(0);
        this.gamesLost = ko.observable<number>(0);
        this.hasFocus = ko.observable<boolean>(false);
        this.hasFocus.subscribe(this.onFocus.bind(this));
    }
}

export = PlayerScore;