import * as ko from "knockout";
import * as mapping from "knockout-mapping";
// import * as _ from "lodash";

import Player = require("./player");
import PlayerScore = require("./playerScore");
import Game = require("./game");
import Comparer = require("../utilities/comparer");

class Data {
    // TODO: separate Data access classes (create these) from
    // this class and Models (restructure accordingly)
    static dataKey: string = "yanivScoreData"

    players: KnockoutObservableArray<Player>
    games: KnockoutObservableArray<Game>
    currentGame: KnockoutObservable<Game>
    version:KnockoutObservable<Date>

    static load():Data {
        if(Data.hasData()) {
            let data = Data.fromJS(JSON.parse(localStorage[this.dataKey]));
            data.version(Data.getDate(data.version()));
            console.log("Loading data");
            console.log("Local data:", Data.toJS(data));
            return data;
        }
        else {
            return new Data();
        }
    }

    static getDate(date:any) {
        if(!date || date.toString() === "Invalid Date") {
            return new Date(0);
        }
        if (typeof date === 'string' || date instanceof String) {
            return new Date(date.toString());
        }
        return date;
    }

    static sync(date1: Data, date2: Data, overwriteDate1: (data:Data) => void,
    overwriteDate2: (data:Data) => void, noUpdate: Function):void {        
        let comparison = Comparer.getComparer((d) => {
            return d.version() ? d.version().getTime() : 0;
        })(date1, date2);

        if(comparison === 1) {
            overwriteDate2(date1);
        }
        else if (comparison === -1) {
            overwriteDate1(date2);
        }
        else {
            noUpdate();
        }
    }

    static save(data: Data, version: Date = null):void {
        data.version(version || new Date());
        localStorage[this.dataKey] = JSON.stringify(Data.toJS(data));
    }

    static toJS(data: Data):any {
        delete data["__ko_mapping__"];
        return ko.toJS(data);
    }

    static fromJS(jsObj):Data {
        return mapping.fromJS(jsObj, {}, new Data());
    }
 
    static hasData():boolean {
        return localStorage[this.dataKey] !== undefined;
    }

    static clear():void {
        localStorage.clear();
    }

    static arrayMatch(a1:any[], a2:any[]):boolean {
        return JSON.stringify(a1) === JSON.stringify(a2);
    }

    static parseInt(number: any) {
        return parseInt(number, 10);
    }

    getPlayerById(id: number):Player {
        return this.players()[id];
    }

    getPlayerByName(name: string):Player {
        return this.players().filter((player) => {
            return player.name() === name;
        })[0];
    }

    getPlayers(playerScores: PlayerScore[]):Player[] {
        return playerScores.map((p) => {
            return this.players()[p.id()];
        }).sort(Player.sortByName);
    }

    getPlayerName(player: Player):string {
        return player.name();
    }

    getGame(selectedPlayers: Player[]):void {
        selectedPlayers = selectedPlayers.sort(Player.sortByName);
        
        let game = this.games().filter((g) => {
            // game which contains the same players
            let gamePlayers = this.getPlayers(g.playerScores());
            return Data.arrayMatch(gamePlayers.map(this.getPlayerName), selectedPlayers.map(this.getPlayerName));
        })[0];

        if(!game) {
            game = new Game();
            game.playerScores(selectedPlayers.map((p) => {
                return new PlayerScore(this.players.indexOf(p));
            }));
            this.games.push(game);
            this.currentGame(this.games()[this.games().length - 1]); // TODO: pass in game directly
        }
        else {
            this.currentGame(game);
        }
    }

    constructor() {
        this.players = ko.observableArray<Player>();
        this.games = ko.observableArray<Game>();
        this.currentGame = ko.observable<Game>(new Game());
        this.version = ko.observable<Date>();
    }
}

export = Data;