//TODO: refactor this and knockout-interaction.ts, make them use TypeScript properly (less 'any')
// - check Knockout typings for ideas
class InteractionController {
    hasTouch: boolean
    element: HTMLElement
    interactions: any
    startPosition: Array<number>
    endPosition: Array<number>

    getTouchCoords(event: any): Array<number> {
        return [
            parseInt(event.changedTouches[0].clientX),
            parseInt(event.changedTouches[0].clientY)
        ];
    }

    getMouseCoords(event: any): Array<number> {
        return [event.pageX, event.pageY];
    }

    getTouchMoveDistance(xy1: Array<number>, xy2: Array<number>): Array<number> {
        return [
            xy2[0] - xy1[0],
            xy2[1] - xy1[1] 
        ];
    }

    hasMoved(xy1: Array<number>, xy2: Array<number>): boolean {
        let dist = this.getTouchMoveDistance(xy1, xy2); 
        return Math.abs(dist[0]) > 10 || Math.abs(dist[1]) > 10;
    }

    start (event: any): void {
        if(this.hasTouch) {
            this.startPosition = this.getTouchCoords(event);
            // console.log("startPosition", this.startPosition);
            event = event.touches[0];
        }
        else {
            this.startPosition = this.getMouseCoords(event);
        }

        if(this.interactions.press) {
            this.interactions.press(event);
        }

        if(this.interactions.move) {
            if(this.hasTouch) {
                this.element.addEventListener("touchmove", this, false);
            }
            else {
                document.addEventListener("mousemove", this, false);
            }
            this.interactions.move(event);
        }

        if(this.interactions.tap || this.interactions.hasOwnProperty("fastFocus")) {
            if(this.hasTouch) {
                this.element.addEventListener("touchend", this, false);
            }
            else {
                document.addEventListener("mouseup", this, false);
            }
        }
    }
    
    // TODO: is this needed?
    move (event: any): void {
        if(this.hasTouch) {
            // let midTouch = this.getTouchCoords(event);
            // console.log("midTouch", midTouch);
            // console.log(this.getTouchMoveDistance(this.startTouch, midTouch));            
            event = event.touches[0];
        }
        this.interactions.move(event);
    }

    end (event: any): void {
        var target = this.hasTouch ? this.element : document;
        var endEvent = this.hasTouch ? "touchend" : "mouseup";
        var moveEvent = this.hasTouch ? "touchmove" : "mousemove";
        
        if(this.hasTouch) {
            this.endPosition = this.getTouchCoords(event);
            // console.log("endPosition", this.endPosition);
            event = event.touches[0];
        }
        else {
            this.endPosition = this.getMouseCoords(event);
        }
        
        if(!this.element.hasAttribute("disabled")) {
            if(!this.hasMoved(this.startPosition, this.endPosition)) {
                console.log("tap");
                
                if(this.interactions.tap)
                    this.interactions.tap(event);

                if(this.interactions.hasOwnProperty("fastFocus")) {
                    setTimeout(() => {
                        this.element.focus();
                    }, 100);
                }
            }
            else {
                console.log("drag");
            }
        }

        target.removeEventListener(endEvent, this, false);
        target.removeEventListener(moveEvent, this, false);
    }

    handleEvent (event: any): void {
        switch (event.type) {
            case 'touchstart': this.start(event); break;
            case 'touchmove': this.move(event); break;
            case 'touchend': this.end(event); break;
            //case 'touchcancel': this.cancel(e); break;
            case 'mousedown': this.start(event); break;
            case 'mousemove': this.move(event); break;
            case 'mouseup': this.end(event); break;
        }
    }
    
    constructor(element: HTMLElement, interactions: Object = null) {
        // detect touch (probably better to test with the actual API)
        this.hasTouch = 'ontouchstart' in window || 'createTouch' in document;        
        this.element = element;
        this.interactions = interactions || {};
        element.addEventListener(this.hasTouch ? "touchstart" : "mousedown", this, false);
        
        // NOTE: weird behaviour here where taps don't work with OSX Chrome
        // when loading from bitbucket and not in mobile mode in dev console.
        // (when using mousedown/mouseup events to detect tap or drag?)
        // Not an issue when loading from file system for some reason!
        // Not a big issue as this only affects desktop
        
        // if(this.hasTouch) {
            //console.log("preventing click for", [element.tagName.toLowerCase(), element.className].join(':'));
            element.addEventListener("click", function(e) {
                // console.log("Prevented click");
                e.preventDefault();
            }, false);
        // }
    }
}

export = InteractionController;