import * as ko from "knockout";
import * as KnockoutBindingHandlers from "knockout-bindingHandlers";

import InteractionController = require("./knockout-interactionController");

// TypeScript-ified version of https://gist.github.com/snaptopixel/5166869
/*
    Mouse/Touch Interactions as bindings for Knockout
    Heavily inspired by Tap.js - https://github.com/alexgibson/tap.js
    */

class KnockoutInteraction {
    getInteractionController(element: any, interactions: any = null): InteractionController {
        var controller = ko.utils.domData.get(element, "interactions");
        if(controller === undefined) {
            controller = new InteractionController(element, interactions);
            ko.utils.domData.set(element, "interactions", controller);
        }
        return controller;
    }

    createShortcutBinding(eventName: any): void {
        // console.log("Creating binding: ", eventName);
        ko.bindingHandlers[eventName] = {
            init: (element, valueAccessor, allBindingsAccessor, viewModel) => {
                let controller = this.getInteractionController(element);
                controller.interactions[eventName] = valueAccessor();
            }
        };
    }

    constructor() {
        ko.bindingHandlers.interaction = {
            init: (element, valueAccessor, allBindingsAccessor, viewModel) => {                
                this.getInteractionController(element, valueAccessor());
            }
        };

        this.createShortcutBinding("press");
        this.createShortcutBinding("tap");
        this.createShortcutBinding("move");
        this.createShortcutBinding("fastFocus");
    }
}

export = KnockoutInteraction;