import * as ko from "knockout";
import Comparer = require("./../utilities/comparer")

class Player {
    name: KnockoutObservable<string>
    // if the player is 'deleted' this flag is set to false and they are hidden
    // (if player deletion is even useful?!)
    enabled: KnockoutObservable<boolean>
    selected: KnockoutObservable<boolean>
    
    static sortByName:(a: Player, b: Player) => number = Comparer.getComparer((p:Player) => p.name());
    
    constructor(name: string, selected: boolean = false) {
        this.name = ko.observable<string>(name);
        this.enabled = ko.observable<boolean>(true);
        this.selected = ko.observable<boolean>(selected);
    }
}

export = Player;