import * as ko from "knockout";
import PlayerScore = require("./playerScore");
import Data = require("./data");

class Game {
    // is this group shown in the selection screen? (when deleted, this is set to false)
    enabled: KnockoutObservable<boolean>
    // track score etc for each player
    playerScores: KnockoutObservableArray<PlayerScore>
    // index of player in Data.players who won the last round     
    lastRoundWinnerIndex: KnockoutObservable<number>
    lastRoundScoreDeductions: KnockoutObservableArray<number>
    // set this when as current time when launching a game as this group, allowing sorting by most recent games
    lastTimePlayed: KnockoutObservable<Date>
    gameNumber: KnockoutObservable<number>
    roundNumber: KnockoutObservable<number>


    constructor() {
        this.enabled = ko.observable<boolean>(true);
        this.playerScores = ko.observableArray<PlayerScore>();
        this.lastRoundWinnerIndex = ko.observable<number>(0);
        this.lastRoundScoreDeductions = ko.observableArray<number>();
        this.gameNumber = ko.observable<number>(1);
        this.roundNumber = ko.observable<number>(1);
    }
}

export = Game;