class Comparer {
    static getComparer(selector: (a, b) => any) {
        return function(a, b) {
            return Comparer.comparer(a, b, selector);
        };
    }

    private static comparer(object1:any, object2:any, selector: Function):number {
        let value1 = selector(object1),
            value2 = selector(object2);

        if(value1 < value2)
            return -1;
        if(value1 > value2)
            return 1;

        return 0;
    }
}

export = Comparer;