import * as ko from "knockout";
import View = require("./../view");
import Data = require("./../data/data");
import Player = require("./../data/player");
import Game = require("./../data/game");
import PlayerScore = require("./../data/playerScore");
import AppViewModel = require("./../appViewModel")
import IViewActions = require("./../interfaces/IViewActions");
import Animate = require("./../utilities/animate");

class GameScores implements IViewActions {
    appViewModel: AppViewModel
    gameOver: KnockoutObservable<boolean>
    lowestScore: KnockoutObservable<string>
    
    onViewSelected():void {
        Animate.animateView(this.appViewModel.currentView());

        let currentGame = this.appViewModel.data().currentGame();
        let players = this.appViewModel.data().players();
        this.gameOver(currentGame.playerScores().filter(p => p.score() > 100).length > 0);
        this.lowestScore(players[currentGame.lastRoundWinnerIndex()].name());
    }

    resetScores():void {
        let currentGame = this.appViewModel.data().currentGame();
        currentGame.playerScores().forEach(p => p.score(0));
    }

    viewGamesLost():void {
        this.resetScores();
        
        this.appViewModel.currentView(View.enterRoundScores);
    }

    nextRound():void {
        this.appViewModel.currentView(View.enterRoundScores);
    }

    rowStatusClass(id: number) {
        let currentGame:Game = this.appViewModel.data().currentGame();
        let playerScores:PlayerScore[] = currentGame.playerScores();
        let deductionIds = currentGame.lastRoundScoreDeductions();
        if(playerScores.filter(p => p.id() === id)[0].score() > 100) {
            return "row bg-danger"; 
        }
        else if (deductionIds.indexOf(id) > -1) {
            return "row bg-success";
        }

        return "row";
    }

    constructor(appViewModel: AppViewModel) {
        this.appViewModel = appViewModel;
        this.gameOver = ko.observable<boolean>(false);
        this.lowestScore = ko.observable<string>();
    }
}

export = GameScores;