import * as ko from "knockout";
import View = require("./../view");
import Data = require("./../data/data");
import Player = require("./../data/player");
import Game = require("./../data/game");
import AppViewModel = require("./../appViewModel");
import IViewActions = require("./../interfaces/IViewActions");
import Animate = require("./../utilities/animate");
class SelectPlayers implements IViewActions {
    appViewModel: AppViewModel
    playerName: KnockoutObservable<string>
    playerNameHasFocus: KnockoutObservable<boolean>
    enabledPlayers: KnockoutComputed<Player[]>
    selectedPlayers: KnockoutComputed<Player[]>
    enableStartGame: KnockoutComputed<boolean>
    
    onViewSelected():void {
        this.appViewModel.data().players().forEach((player) => {
            player.selected(false);
        });
        Animate.animateView(this.appViewModel.currentView());
    }

    onKeyUp(event: KeyboardEvent, context: SelectPlayers):void {
        switch(event.keyCode) {
            case 13:
                context.addPlayerSubmit();
                break;
        }
    }

    select(player: Player) {
        player.selected(!player.selected());
    }

    delete(player: Player) {
        player.enabled(false);
        player.selected(false);
    }

    rowClass(player: Player) {
        // console.log("burrito");
        return player.selected() ? "row bg-success" : "row";
    }

    bindEvents():void {
        this.appViewModel.events.bindEvent(this, "keyup", this.onKeyUp, View.selectPlayers);
    }

    addPlayerSubmit():void {
        let playerName = this.playerName().trim(); 
        let player = this.appViewModel.data().getPlayerByName(playerName);

        if(playerName === "") {
            console.log("Player name input is empty");
        }
        else if(player) {
            player.enabled(true);
            player.selected(true);
        }
        else {
            this.appViewModel.data().players.push(new Player(playerName, true));
        }

        this.playerName("");
    }

    startGame():void {
        this.appViewModel.data().getGame(this.selectedPlayers());
        this.appViewModel.saveData();
        this.appViewModel.currentView(View.gameScores);
    }

    computedEnabledPlayers():KnockoutComputed<Player[]> {
        return ko.computed<Player[]>(() => {
            return this.appViewModel.data().players().filter((player) => {
                return player.enabled();
            }).reverse();
        });
    }

    computedSelectedPlayers():KnockoutComputed<Player[]> {
        return ko.computed<Player[]>(() => {
            return this.appViewModel.data().players().filter((player) => {
                return player.selected();
            });
        });
    }

    computedEnableStartGame():KnockoutComputed<boolean> {
        return ko.computed<boolean>(() => {
            return this.selectedPlayers().length > 1;
        });
    }

    constructor(appViewModel: AppViewModel) {
        this.appViewModel = appViewModel;
        this.enabledPlayers = this.computedEnabledPlayers();
        this.selectedPlayers = this.computedSelectedPlayers();
        this.enableStartGame = this.computedEnableStartGame();
        this.playerName = ko.observable<string>("");
        this.playerNameHasFocus = ko.observable<boolean>(false);
        this.bindEvents();
    }
}

export = SelectPlayers;