import View = require("./../view");

class Animate {
    static delay:number = 80;

    static animateView(view:View, callback:Function = null):void {
        // setTimeout(() => {
        let bounceInUpSelector = `#${View[view]} .striped-rows .row, #${View[view]} .animate-in, #${View[view]} h2`;
        // let bounceInSelector = `#${View[view]} h2`;
        let bounceInUpElements = this.getElements(bounceInUpSelector);
        this.bounceInUp(bounceInUpElements, callback);

        // let bounceInElements = this.getElements(bounceInSelector);
        // this.bounceIn(bounceInElements);
        
    }

    private static getElements(selector:string) {
        return Array.prototype.slice.call(document.querySelectorAll(selector)) as Array<HTMLElement>;
    }

    private static removeAnimatedClasses(element: HTMLElement):void {
        element.className = element.className.split("animated")[0];
    }

    static bounceIn(elements: Array<HTMLElement>, callback:Function = null) {
        elements.forEach((element:HTMLElement) => {
            this.removeAnimatedClasses(element);
        });
        this.bounceInElement(elements, 0, callback);
    }

    private static bounceInElement(elements: Array<HTMLElement>, index:number, callback:Function = null) {
        setTimeout(() => {
            let element = elements[index];
            element.className = `${element.className} animated bounceIn`;
            if(index < elements.length - 1)
                this.bounceInElement(elements, index + 1, callback);
            else if(callback)
                callback();
        }, index > 0 ? this.delay : 0);
    }

    static bounceInUp(elements: Array<HTMLElement>, callback:Function = null) {
        elements.forEach((element:HTMLElement) => {
            element.style.display = "none";
            this.removeAnimatedClasses(element);
        });
        this.bounceInUpElement(elements, 0, callback);
    }

    private static bounceInUpElement(elements:Array<HTMLElement>, index:number, callback:Function = null) {
        setTimeout(() => {
            let element = elements[index];
            element.removeAttribute("style");
            element.className = `${element.className} animated bounceInUp`;
            if(index < elements.length - 1)
                this.bounceInUpElement(elements, index + 1, callback);
            else if(callback)
                callback();
        }, index > 0 ? this.delay : 0);
    }
}

export = Animate;