import * as ko from "knockout";

import View = require("./view");
import Data = require("./data/data");
import Events = require("./events");
import Animate = require("./utilities/animate");

// view actions
import ChooseGroup = require("./actions/chooseGroup");
import SelectPlayers = require("./actions/selectPlayers");
import Groups = require("./actions/groups");
import EnterRoundScores = require("./actions/enterRoundScores");
import ScoreDeduction = require("./actions/scoreDeduction");
import GameScores = require("./actions/gameScores");
import GameOver = require("./actions/gameOver");
import Logo = require("./actions/logo");
import Login = require("./actions/login");

class AppViewModel {
    currentView: KnockoutObservable<number>
    data: KnockoutObservable<Data>
    events: Events
    bindingElement:HTMLElement
    
    // view actions, matching names of View enum values
    // for auto integration with view switching via currentView()
    // - show/hide views based on currentView
    // - trigger onViewSelected in relevant view actions class
    // (this could be taken further to auto-map View enum values
    // to instances of view action classes in this class)
    chooseGroup: ChooseGroup
    groups: Groups
    selectPlayers: SelectPlayers
    enterRoundScores: EnterRoundScores
    gameOver: GameOver
    scoreDeduction: ScoreDeduction
    gameScores: GameScores
    logo: Logo
    login: Login

    saveData():void {
        Data.save(this.data());
    }

    reload():void {
        window.location.href = window.location.href;
    }

    // currentView() magic (as described above)
    bindViewSelection():void {        
        this.currentView = ko.observable(View.logo);
        this.currentView.subscribe(() => {
            var viewName = View[this.currentView()];
            console.log("View selected:", viewName);
            this[viewName].onViewSelected(); // trigger event to initialise view            
        });

        // automatically show/hide views based on currentView value
        // currentView can be set via the View enum, e.g.
        // this.currentView(View.chooseGroup);
        let views = document.getElementsByClassName("view");
        for(var i = 0; i < views.length; i++) {
            let viewIndex = View[views[i].id];
            views[i].setAttribute("data-bind", "visible: currentView() === " + viewIndex);
        }
    }

    // instantiate view action classes
    actions():void {
        this.chooseGroup = new ChooseGroup(this);
        this.groups = new Groups(this);
        this.selectPlayers = new SelectPlayers(this);
        this.enterRoundScores = new EnterRoundScores(this);
        this.gameOver = new GameOver(this);
        this.scoreDeduction = new ScoreDeduction(this);
        this.gameScores = new GameScores(this);
        this.logo = new Logo(this);
        this.login = new Login(this);
    }

    constructor(bindingElement: HTMLElement) {
        this.bindingElement = bindingElement;
        this.bindViewSelection();  
        this.data = ko.observable<Data>(Data.load()); // load data into view model
        this.events = new Events(this);
        this.actions();
        ko.applyBindings(this, bindingElement);
    }
}

export = AppViewModel;