import * as ko from "knockout";
import View = require("./../view");
import Data = require("./../data/data");
import AppViewModel = require("./../appViewModel");
import IViewActions = require("./../interfaces/IViewActions");

class ChooseGroup implements IViewActions {
    appViewModel: AppViewModel
    showPreviousGames: KnockoutComputed<boolean>
    showClearData: KnockoutObservable<boolean>

    onViewSelected():void {
        
    }

    clearData() {
        Data.clear();
        this.appViewModel.reload();
    }

    computedShowPreviousGames():KnockoutComputed<boolean> { 
        return ko.computed<boolean>(() => {
            return this.appViewModel.data().games().length > 0;
        });
    }

    constructor(appViewModel: AppViewModel) {
        this.appViewModel = appViewModel;
        this.showPreviousGames = this.computedShowPreviousGames();
        this.showClearData = ko.observable(Data.hasData());
    }
}

export = ChooseGroup;