import * as ko from "knockout";
import View = require("./../view");
import Data = require("./../data/data");
import AppViewModel = require("./../appViewModel")
import IViewActions = require("./../interfaces/IViewActions");
import Animate = require("./../utilities/animate");

declare var Auth0:any; // don't enforce typings for this lib
declare var gapi:any; // don't enforce typings for this lib
declare var GDriveAppData:any;

class Login implements IViewActions {
    appViewModel: AppViewModel
    loginStatus: KnockoutObservable<string>
    queryStrings: any
    googleClientId: string = "258167888533-t5dt8ldgvv8mtd5c98sr5b2ier1q0go8.apps.googleusercontent.com";
    auth0ClientId: string = "1ZLbsANiYd3PhNyrVnJYVzK6GO5mrag4";
    accessToken: string
    showPrompt: KnockoutObservable<boolean>

    onViewSelected():void {
        if(localStorage.getItem("drive_sync")) {
            this.sync();
        }
        Animate.animateView(this.appViewModel.currentView());
    }

    getQueryStrings() {
        this.queryStrings = {};
        
        let qs = window.location.search.substr(1).split("&");
        qs.forEach((kvpString) => {
            let kvp = kvpString.split("=");
            this.queryStrings[kvp[0].toLowerCase()] = decodeURIComponent(kvp[1]);
        });
    }

    getQueryString(key: string) {
        return this.queryStrings[key.toLowerCase()];
    }

    ajaxGet(url: string, headers: Object, success: Function = null, failure: Function = null) {
        let xhr = new XMLHttpRequest();
        xhr.open("GET", url, true);
        Object.keys(headers).forEach((key) => {
            xhr.setRequestHeader(key, headers[key]);
        });
        xhr.onreadystatechange = () => {
            console.log(xhr.responseBody);
            if(success)
                success(xhr);
        };
        xhr.onerror = () => {
            console.log(xhr.responseBody);
            if(failure)
                failure(xhr);
        };
        xhr.send();
    }

    gdataSave(data: Data) {
        GDriveAppData.saveAppData(Data.toJS(data));
    }

    gdataLoaded(backupDataObj:any):void {
        console.log("-_-");
        console.log("Backup data obj:", backupDataObj);
        let backupData;
        if(!backupDataObj) {
            console.log("Invalid backup, recreating");
            backupData = Data.fromJS({ version: new Date(0) });
            console.log("Backup data:", Data.toJS(backupData));
        }
        else {
            try {
                backupDataObj.version = Data.getDate(backupDataObj.version);
                backupData = Data.fromJS(backupDataObj)
                console.log("Backup data:", backupData);
                console.log("Backup data JS:", Data.toJS(backupData));
            }
            catch (err) {
                console.log(err);
                backupData = Data.fromJS({ version: new Date(0) });
            }
        }

        Data.sync(this.appViewModel.data(), backupData, (backupData) => {
            console.log("Updating local data");
            console.log(backupData);
            this.appViewModel.data(backupData);      
            Data.save(backupData, backupData.version());
        }, (localData) => {
            console.log("Updating backup");
            this.gdataSave(localData);
        }, () => {
            console.log("Both files are in sync");
        });

        this.appViewModel.currentView(View.selectPlayers);
    }

    gdataLoad() {
        GDriveAppData.loadAppData().then(this.gdataLoaded.bind(this), (e) => {
            console.log("Failed to load data", e);

            // ensure an authorization popup is shown where user can login and authorize your app
            GDriveAppData.config.immediateAuth = false;
            // load the app data again
            GDriveAppData.loadAppData().then(this.gdataLoaded.bind(this), (x) => {
                console.log("Failed to load data with immediateAuth = false", x);
                this.appViewModel.currentView(View.selectPlayers);
            });
        });
    }

    sync():void {
        localStorage.setItem("drive_sync", "true");
        GDriveAppData.config.clientId = this.googleClientId;
        GDriveAppData.config.appDataFileName = "data.json";

        this.showPrompt(false);

        var auth0 = new Auth0({
            domain: "zplume.eu.auth0.com",
            clientID: this.auth0ClientId,
            callbackURL: window.location.href.replace(/\/$/, "").replace(/^http:/, "https:"), // remove trailing slash, force HTTPS
            responseType: "token"
        });

        this.getQueryStrings();
        let code = this.getQueryString("code");
        if(code) {
            this.loginStatus("Signed in");
            // parse the id_token
            localStorage.setItem("id_token", code);
            console.log("id_token:", code);

            // redirect back to home page without ? or #
            window.location.href = window.location.href.split('?')[0];
        }
        else if(!localStorage.getItem('id_token')) {
            this.loginStatus("Signing in...");
            // no token in localStorage, go get it
            auth0.signin({ connection: 'google-oauth2' }); // do sign-in redirect: google -> auth0 -> app
        }
        else {            
            // we're not handling the callback or requiring a token, sync data
            this.loginStatus("Checking for data backup...");
            this.gdataLoad();
        }
    }

    skip():void {
        this.appViewModel.currentView(View.selectPlayers);
    }

    constructor(appViewModel: AppViewModel) {
        this.appViewModel = appViewModel;
        this.loginStatus = ko.observable<string>("");
        this.loginStatus.subscribe((value:string) => {
            Animate.bounceInUp([document.getElementById("loginStatus")]);
        });
        this.showPrompt = ko.observable<boolean>(true);
    }
}

export = Login;