import * as ko from "knockout";
import View = require("./../view");
import Data = require("./../data/data");
import PlayerScore = require("./../data/playerScore");
import AppViewModel = require("./../appViewModel");
import IViewActions = require("./../interfaces/IViewActions");

class GameOver implements IViewActions {
    appViewModel: AppViewModel
    losers: KnockoutObservableArray<PlayerScore>

    onViewSelected():void {
        // TODO: something fancy with animate.css
        // TODO: show losers?
        let currentGame = this.appViewModel.data().currentGame(); 
        this.losers(currentGame.playerScores().filter(p => p.score() > 100));
        // TOOD: update view to show losers

        setTimeout(() => {
            this.losers.removeAll();
            this.appViewModel.currentView(View.gameScores);
        }, 1000);
    }

    constructor(appViewModel: AppViewModel) {
        this.appViewModel = appViewModel;
        this.losers = ko.observableArray<PlayerScore>();
    }
}

export = GameOver;