import * as ko from "knockout";
import View = require("./view");
import AppViewModel = require("./appViewModel")

class Events {
    appViewModel: AppViewModel
    errors: KnockoutObservableArray<string>

    fireEvent(context: any, handler: Function, view: View, event: Event):void {
        if(this.appViewModel.currentView() === view)
            handler(event, context);
    }

    // Add an event listener that only fires for a specific view.
    // context is passed to the handler as 'this' in the handler
    // won't refer to the class that defines the handler
    bindEvent(context: any, eventName: string, handler: Function, view: View):void {
        document.addEventListener(eventName, this.fireEvent.bind(this, context, handler, view));
    }

    onError() {
        this.displayError(JSON.stringify(arguments));
    }

    displayError(message) {
        this.errors.push(message);
        setTimeout(() => {
            this.errors.shift();
        }, 2000);
    }
    
    constructor(appViewModel: AppViewModel) {
        this.appViewModel = appViewModel;
        this.errors = ko.observableArray<string>();
        window.onerror = this.onError.bind(this);
    }
}

export = Events;