// cobbled together from tips + code at http://www.html5rocks.com/en/tutorials/appcache/beginner
import AppViewModel = require("./appViewModel");

class AppCache {
    private static appViewModel:AppViewModel

    private static onPageReady:Function

    private static handleCacheEvent(e) {        
        console.log(e);
        AppCache.appViewModel.logo.showStatus(e.type);
    }

    private static handleCacheError(e) {
        AppCache.handleCacheEvent(e);
    }

    private static logEvents() {
        let appCache = window.applicationCache;

        // Fired after the first cache of the manifest.
        appCache.addEventListener('cached', AppCache.handleCacheEvent, false);

        // Checking for an update. Always the first event fired in the sequence.
        appCache.addEventListener('checking', AppCache.handleCacheEvent, false);

        // An update was found. The browser is fetching resources.
        appCache.addEventListener('downloading', AppCache.handleCacheEvent, false);

        // The manifest returns 404 or 410, the download failed,
        // or the manifest changed while the download was in progress.
        appCache.addEventListener('error', AppCache.handleCacheError, false);

        // Fired after the first download of the manifest.
        appCache.addEventListener('noupdate', AppCache.handleCacheEvent, false);

        // Fired if the manifest file returns a 404 or 410.
        // This results in the application cache being deleted.
        appCache.addEventListener('obsolete', AppCache.handleCacheEvent, false);

        // Fired for each resource listed in the manifest as it is being fetched.
        appCache.addEventListener('progress', AppCache.handleCacheEvent, false);

        // Fired when the manifest resources have been newly redownloaded.
        appCache.addEventListener('updateready', AppCache.handleCacheEvent, false);
    }

    private static attachEvents() {
        let appCache = window.applicationCache;

        appCache.addEventListener("updateready", (e) => {
            if (appCache.status === appCache.UPDATEREADY) {
                appCache.swapCache(); // calling applicationCache.swapCache() will swap the old cache for the new one
                console.log("App cache updated, reloading page to load changes");
                this.appViewModel.logo.showStatus("Update downloaded");
                window.location.reload();
            }
            else {
                this.appViewModel.logo.showStatus("Update not ready");
            }
        }, false);

        appCache.addEventListener('progress', (e:ProgressEvent) => {
            let update = `Downloading update ${e.loaded}/${e.total}`;
            console.log(update);
            this.appViewModel.logo.showStatus(update);
        }, false);

        appCache.addEventListener("cached", () => {
            this.appViewModel.logo.showStatus("Installation complete");
            AppCache.onPageReady();
        }, false); // called on first load

        appCache.addEventListener("noupdate", () => {
            this.appViewModel.logo.showStatus("No updates available");
            AppCache.onPageReady();
        }, false); // called on subsequent loads

        appCache.addEventListener("error", (e) => {
            this.appViewModel.events.displayError(e);
            AppCache.onPageReady(true);
        }, false); // called on error (duh)

        appCache.addEventListener("obsolete", () => {
            // this.appViewModel.logo.showStatus("?");
            AppCache.onPageReady();
        }, false); // 

        if(window.location.href.indexOf("file://") === 0) {
            this.appViewModel.logo.showStatus("Bypassing AppCache");
            AppCache.onPageReady();
        }

        // iOS chrome sucks
        setTimeout(() => {
            // wat
            if(appCache.status === appCache.UNCACHED) {
                AppCache.onPageReady();
            }    
        }, 1000);
    }

    static processUpdates(appViewModel: AppViewModel, callback: Function) {
        AppCache.appViewModel = appViewModel;
        AppCache.onPageReady = callback;
        // AppCache.logEvents();

        this.appViewModel.logo.showStatus("AppCache initialised");
        // Check if a new cache is available
        AppCache.attachEvents();
    }
} 

export = AppCache;