import * as ko from "knockout";
import View = require("./../view");
import Data = require("./../data/data");
import Game = require("./../data/game");
import Player = require("./../data/player");
import PlayerScore = require("./../data/playerScore");
import AppViewModel = require("./../appViewModel");
import IViewActions = require("./../interfaces/IViewActions");
import Animate = require("./../utilities/animate");

class EnterRoundScores implements IViewActions {
    appViewModel: AppViewModel
    playerScores: KnockoutObservableArray<PlayerScore>
    enableSaveScores: KnockoutComputed<boolean>

    onViewSelected():void {
        this.getCurrentGameScores();
        this.playerScores().forEach(p => p.score.subscribe(this.onScoreChanged.bind(this, p)));
        Animate.animateView(this.appViewModel.currentView(), () => {
            this.autoFocusFirstPlayerScore();
        });
    }

    onScoreChanged(playerScore: PlayerScore):void {
        //console.log("Player score update", playerScore.score());
        if(playerScore.score() !== undefined) {
            let score:string = playerScore.score().toString().replace(/[^0-9]/g, "").slice(0, 3); // numbers only, first 3
            if(score !== playerScore.score().toString())
                playerScore.score(Data.parseInt(score));
        }
    }

    autoFocusFirstPlayerScore(): void {
        setTimeout(() => {
            this.playerScores()[0].hasFocus(true);
        }, 200);
    }

    getCurrentGameScores(): void {
        this.playerScores(this.appViewModel.data().currentGame().playerScores().map((p) => {
            return new PlayerScore(p.id());
        }));
    }

    getRoundWinnerId(currentGame: Game): void {
        this.playerScores().forEach((p) => {
            if(!PlayerScore.hasScore(p)) { 
                p.score(0); // convert undefined or "" to 0 for the winner, if there is one
            }            
        });
        // TODO: this doesn't account for a tie on lowest score, it should
        currentGame.lastRoundWinnerIndex(this.playerScores().sort(PlayerScore.sortByScore)[0].id());
    }

    getPlayerRoundScoreById(id: number):PlayerScore {
        return this.playerScores().filter(roundScore => roundScore.id() === id)[0];
    }

    addPlayerRoundScoreToGameScore(playerGameScore: PlayerScore): void {
        let playerRoundScore = this.getPlayerRoundScoreById(playerGameScore.id());
        playerGameScore.score(Data.parseInt(playerRoundScore.score()) + Data.parseInt(playerGameScore.score()));
    }

    deduct25OnMultipleOf25(playerGameScore: PlayerScore, currentGame: Game): void {
        if(this.getPlayerRoundScoreById(playerGameScore.id()).score() === 0)
            return;

        if(playerGameScore.score() === 25 || playerGameScore.score() === 50 ||
        playerGameScore.score() === 75 || playerGameScore.score() === 100) {
            playerGameScore.score(Data.parseInt(playerGameScore.score()) - 25);                
            currentGame.lastRoundScoreDeductions.push(playerGameScore.id());
        }
    }

    saveScores():void {
        let currentGame:Game = this.appViewModel.data().currentGame();
        let someoneLost:boolean = false;
        
        this.getRoundWinnerId(currentGame);
        currentGame.roundNumber(Data.parseInt(currentGame.roundNumber()) + 1);
        currentGame.lastRoundScoreDeductions.removeAll();

        currentGame.playerScores().forEach((playerGameScore) => {
            this.addPlayerRoundScoreToGameScore(playerGameScore);
            this.deduct25OnMultipleOf25(playerGameScore, currentGame);

            if(playerGameScore.score() > 100) {
                playerGameScore.gamesLost(Data.parseInt(playerGameScore.gamesLost()) + 1);
                someoneLost = true;
            }
        });

        //document.body.blur();

        console.log("someoneLost:", someoneLost);
        if(someoneLost === true) {
            currentGame.gameNumber(Data.parseInt(currentGame.gameNumber()) + 1);
            this.appViewModel.saveData();
            // this.appViewModel.currentView(View.gameOver);
            // return;
        }
        
        this.appViewModel.saveData();
        console.log("lastRoundScoreDeductions:", currentGame.lastRoundScoreDeductions().length);
        if(currentGame.lastRoundScoreDeductions().length > 0) {
            // this.appViewModel.currentView(View.scoreDeduction);
            // return;
        }
        
        this.appViewModel.currentView(View.gameScores);
    }

    computedEnableSaveScores() {
        return ko.computed<boolean>(() => {
            return this.playerScores().filter((p) => {
                return !PlayerScore.hasScore(p) || p.score().toString() === "0";
            }).length <= 1;
        });
    }

    constructor(appViewModel: AppViewModel) {
        this.appViewModel = appViewModel;
        this.playerScores = ko.observableArray<PlayerScore>();        
        this.enableSaveScores = this.computedEnableSaveScores();

        //if(this.playerScores.getSubscriptionsCount() === 0)
        
    }
}

export = EnterRoundScores;